html, body {
  scroll-behavior: smooth;
  font-family: "Graphik-Bold", sans-serif;
}
.container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  pointer-events: none;
  justify-content: start !important;
  font-family: 'Kanit', sans-serif;
  line-height: 0px;
  text-transform: uppercase;
}

.number {
  font-size: 450px;
  color: #373c4c;
}

.video-react-big-play-button {

	height: 90px !important;
	width: 90px !important;
	background-color: rgba(255, 255, 255, 1) !important;
  border-radius: 50px !important;
  border: 0px !important;
}
.video-react-big-play-button:hover {
	background-color: #fef262 !important;
}

.play-button:after {
    content: "";
    display: block;
    position: absolute;
    top: 32px;
    left: 37px;
    margin: 0 auto;
    border-style: solid;
    border-width: 12.5px 0 12.5px 20px;
    border-color: transparent transparent transparent rgba(0, 0, 0, 1);
    color: black;

}

.video-react-big-play-button svg {
  color: black !important;
  margin-top: 33px;
  margin-left: 3px;

}
.number span {
  display: inline-block;
  position: relative;
  transform: translate3d(-35%, 0, 0);
}

.header {
  margin-left: 350px;
  font-size: 70px;
  color: white;
}

.stripe {
  height: 2px;
  width: auto;
}

.slopeBegin {
  background-color: #20232f;
  clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
}

.slopeEnd {
  clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 140%;
  height: 100%;
  cursor: pointer;
}


#root {
  background-color: #000000;
  -webkit-transition: background-color 2s; /* For Safari 3.1 to 6.0 */
  transition: background-color 2s;
}


.pink {
  background: linear-gradient(to right, deeppink 0%, coral 100%);
}

.teal {
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

.tomato {
  background: linear-gradient(to right, tomato 0%, gold 100%);
}

