.gallery-container {
    /* padding: .9375rem 0; */
}



.gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 0px);
    grid-gap: 24px;
    max-width: 1200px;
    width: 100%;
    margin: 50px auto;
}

@media (min-width: 20em) {
    .gallery-grid {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media (min-width: 34em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 60em) {
    .gallery-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

.gallery-grid img {
    width: 100%;
    /* padding-bottom:10px; */
    object-fit: cover;

}

.grid2x2 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    max-height: 96.6%;

  }
  .grid2x2 > div {
    display: flex; 
    flex-basis: calc(50% - 12px);  
    justify-content: center;
    flex-direction: column;
  }
  .grid2x2 > div > div {
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  
  .box { margin: 0px;  
         width: calc(50%-12px);
  }
  .box1 {
      margin-right:12px;
      margin-bottom:12px;


  }

  .box2 {
    margin-left:12px;
    margin-bottom:12px;

  }

  .box3 {
    margin-right:12px;
    margin-top:12px;

  }


  .box4 {
    margin-left:12px;
    margin-top:12px;

  }
  
